


















import PopupMixin from '@/mixins/popup'
import { Component, Mixins, Prop } from 'vue-property-decorator'
import 'jodit/build/jodit.min.css'
import { API_URL } from '@/configs'

// @ts-ignore
import { JoditEditor } from 'jodit-vue'
@Component({ components: { JoditEditor } })
export default class PopupNote extends Mixins(PopupMixin) {
  @Prop({
    required: false,
    type: Object,
    default: () => {
      return {}
    }
  })
  post!: Record<string, any>
  @Prop({ required: true, type: String, default: 'add' }) type!: string
  buttons = ['bold', 'italic', 'underline', 'link', 'ul', 'ol', 'image']
  config = {
    spellcheck: false,
    showCharsCounter: false,
    showWordsCounter: false,
    autofocus: this.type === 'add' ? true : false,
    showXPathInStatusbar: false,
    language: 'vi',
    minHeight: 170,
    i18n: {
      vi: {
        'Type something': 'Nhập ghi chú...',
        'Search for': 'Nhập tìm kiếm'
      }
    },
    uploader: {
      url: `${API_URL}/file/v1/district/${this.$store.state.beUser.districtId}/upload`,
      format: 'json',
      headers: {
        Authorization: `Bearer ${this.$store.state.beAuth.access_token}`
      },
      isSuccess: function (resp: Record<string, any>): any {
        return !resp.error
      },
      getMessage: function (resp: Record<string, any>): string {
        return resp.msg
      },
      filesVariableName: function (e: any): string {
        return 'files'
      },
      process: function (resp: Record<string, any>): Record<string, any> {
        return {
          files: resp.data || []
        }
      },
      defaultHandlerSuccess: function (data: Record<string, any>): void {
        const field = 'files'
        console.log('res', data)

        if (data[field] && data[field]['success'].length) {
          for (let i = 0; i < data[field]['success'].length; i += 1) {
            console.log('hi', data[field]['success'][i].url)

            //@ts-ignore
            this.s.insertImage(data[field]['success'][i].url)
          }
        }
      },
      error: function (e: any): void {
        console.error(e)
      }
    }
  }

  content = ''

  handleOpen(): void {
    if (this.type === 'edit') {
      this.content = this.post.content
    } else {
      this.content = '<p><br></p>'
    }
  }

  handleSave(): void {
    let post: Record<string, any> = {}
    if (this.type === 'edit') {
      post = { ...this.post, content: this.content }
    } else {
      post = { content: this.content }
    }
    this.$emit('update', post)
    this.setOpenPopup({
      popupName: 'popup-note',
      isOpen: false
    })
  }
  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-note',
      isOpen: false
    })
  }
}
