                this.listCustomer.push(this.dataRow)
              } else if (this.dataRows) {
                this.listCustomer.push(this.dataRows)
              } else if (this.dataDetail) {
                this.listCustomer.push(this.dataDetail)
              } else {
                this.listCustomer = res.content
              }
            }
          }
        })
    } catch (error) {
      this.isLoading = false
      this.listCustomer = []
    }
  }
