  handleClickSave(): void {
    let a: any = this.$refs.dataInput
    if (this.type === 'create' && this.fileList.length) {
      this.dataInput.attachFiles = [...this.fileList]
    }
    a.validate((valid: any) => {
      if (valid) {
        if (this.type === 'create') {
          this.fileList = []
          this.dataInput.dueTime += ':00'
          const params = {
            ...this.dataInput,
            attendanceContractIds: this.dataInput.attendanceContractIds?.length ? this.dataInput.attendanceContractIds.join(',') : null
          }
          console.log('hasagi', params)
          this.api
            ?.createAppoint(params)
            .then(() => {
              this.$message.success('Tạo lịch hẹn thành công')
              this.$emit('apply')
              this.handleClose()
            })
            .catch(er => {
              this.$message.error(er.message)
            })
        } else {
          if (this.isEditDate) {
            this.dataInput.dueDate += 'T00:00:00.000+00:00'
          }
          // const tempDate = this.$options.filters?.formatDateHourReverse(this.dataInput.dueDate)
          this.dataInput.dueTime += ':00'
          // this.dataInput.files = this.
          this.dataInput.files = this.fileList
          this.$emit('update', this.dataInput)
          this.handleClose()
        }
      } else {
        console.log('error validate data!')
        return false
      }
    })
  }
  handleReset(): void {
