  handleClose(): void {
    // @ts-ignore
    this.$refs.dataInput.clearValidate()
    this.isEditDate = false
    this.isEditTime = false
    this.dataInput.attendanceContractIds = {}
    this.setOpenPopup({
      popupName: 'appoint-customer',
      isOpen: false
    })
    this.fileList = []
  }
  async handleOpen(): Promise<void> {
    // this.idCustomer = this.$route.params.id
    try {
      await Promise.all([this.getEmployeeList(''), this.getListCustomer('')])
      if (this.type === 'edit') {
        this.dataInput = { ...this.post }
        this.dataInput = { ...this.post }
        this.fileList = this.post.files || []

        this.dataInput.dueTime = this.$options.filters?.getHoursString(this.post.dueTime)
      } else {
        this.dataInput = {}
        //@ts-ignore
        this.$refs.dataInput.clearValidate()
      }
    } catch (error) {
      console.log(error)
    }
    this.getAsignMember()
    if (this.idCustomer) {
      this.getListSalesDeals('')
      this.getListContacts('').then(() => {
        this.getFistContact()
      })
      this.dataInput.customerId = parseInt(this.idCustomer)
    }
  }

